* {
    box-sizing: border-box;
    padding: 0px;
    margin: 0px;
}

body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
/* ---------------------------------------------
counter
    ----------------------------------------- */
.content-counter {
    margin: auto;  
    height: 150px;
    margin-top: 60px;
}

#counter, #counter-anim {
    text-align: center;
    margin: auto;
    -webkit-transition: 500ms ease-in-out;
    transition: 500ms ease-in-out;
}

/* ==================================================
counter-wrapper
    =============================================== */
.counter-wrapper {
    width: 600px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    -webkit-transition: 500ms;
    transition: 500ms;
    color: white;
    flex-direction: row;   
}
.counter-wrapper > .time-content {
    background: rgba(197, 192, 192, 0.3);
    box-shadow: 0px 0px 20px 2px rgba(255, 255, 255, 0.52);
    width: 120px;
    height: 120px;
    margin: 15px;
    border: solid 2px transparent;
    position: relative;
    overflow: hidden;
}
.counter-wrapper > .time-content > .content-number {
    font-size: 50px;
    line-height: 80px;
}

/*==========================================
    counter animation 
    =======================================*/
.anim {
    position: absolute;
    left: -50%;
    background: -webkit-radial-gradient(transparent 56%,rgb(255, 255, 255));
    background: radial-gradient(transparent 56%,rgb(255, 255, 255));
    display: block;
    overflow: hidden;
    -webkit-animation: rotate 7s infinite linear;
            animation: rotate 7s infinite linear;
    width: 10px;
    height: 10px;
}


@keyframes rotate {
    0% { -webkit-transform:rotate(360deg); transform:rotate(360deg); }
    100% { -webkit-transform:rotate(0deg); transform:rotate(0deg); }
}   
@-webkit-keyframes rotate {
    0% { -webkit-transform:rotate(360deg); transform:rotate(360deg); }
    100% { -webkit-transform:rotate(0deg); transform:rotate(0deg); }
}   


/* ==================================================
Media Queries - Tablets
============================================== */
@media screen and (min-width: 768px) and (max-width: 1024px) {
    .counter-wrapper {
        width: 90%;
    }
}

@media screen and (max-width:767px ) {
    .counter-wrapper {
        width: 90%;
    }
    .counter-wrapper > .time-content {
        width: 100px;
        min-width: 100px;
        height: 100px;
        margin-left: 10px;
    }
    .counter-wrapper > .time-content > .content-number {
        font-size: 40px;
        line-height: 65px;
    }
    .counter-wrapper > .time-content >  .time-text {
        font-size: 10px;
    }
}
/* ==================================================
   Responsive Media Queries - Mobiles
================================================== */

@media screen and (max-width: 480px) {
    .counter-wrapper {
        width: 80%;
    }
    .counter-wrapper > .time-content {
        width: 60px;
        min-width: 60px;
        height: 60px;
        margin: 6px;
        box-shadow: 0 0 2px 3px rgba(255, 255, 255, 0.25);       
    }
    .counter-wrapper > .time-content > .content-number {
        font-size: 20px;
        margin: 8px 0 0 ;
        line-height: 20px;
    }
    .counter-wrapper > .time-content > .time-text {
        font-size: 10px;
    }
}

.app {

}

.app-root {
    box-sizing: border-box;
    width: 100%;
    min-height: 100vh;
    overflow: hidden;
    background: rgb(35, 39, 65);

    display: flex;
    justify-content: center;
    align-items: center;
}

.app-particle__container {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    top: 0;
    z-index: 1;
}

.app-particle {
    width: 100%;
    height: 100%;
}

.app-container {
    z-index: 10;
}

.app-logo {
    position: absolute;
    top: 60px;
    width: 100px;
    height: 100px;
    /*animation: app-logo-spin infinite 20s linear;*/
}

div.centered {
    color: #fff;
    text-shadow: 0 1px 0 #333, -1px 0 0 #333, 0 0 10px rgba(0,0,0,.8);
    font-weight: 100;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    -webkit-transform: scaleY(.8);
            transform: scaleY(.8);
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    font-size: 55px;
}

@-webkit-keyframes app-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes app-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

